.navbar {
  display: flex;
  align-items: center;
  height: 50px;
  border-bottom: 0.5px solid lightgray;
  font-size: 14px;
  color: #555;
  .wrapper {
    width: 100%;
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
  }

  .search {
    display: flex;
    align-items: center;

   padding: 3px;
   border: 0.5px solid lightgray;
   input {
    border: none;
    outline: none;
    background-color: transparent;
    &::placeholder {
        font-size: 12px;
    }
   }
  }
  .items {
    display: flex;
    align-items: center;
    .item {
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;
        cursor: pointer;
        .icon {
            font-size: 20px;
          &:hover {
            scale: 1.25;
          }
        }
        .avatar {
            width: 30px;
            height: 30px;
            border-radius: 50%;
        }
        .counter {
            width: 15px;
            height: 15px;
            background-color: red;
            border-radius: 50%;
            color: white;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 10px;
            font-weight: bold;
            position: absolute;
            top: -5px;
            right: -5px;
            opacity: 0.9;

        }
    }

  }
}
